import { Component, Prop, h, Host } from '@stencil/core';

export type TrendDirection = 'positive' | 'negative' | 'neutral';
export type TrendVariant = 'line-only' | 'fill-only' | 'line-fill';

@Component({
  tag: 'bh-trend-chart',
  styleUrl: 'bh-trend-chart.css',
  shadow: true,
})
export class BhTrendChart {
  /**
   * The trend direction (affects color)
   */
  @Prop() trend: TrendDirection = 'positive';

  /**
   * Data points for the chart
   */
  @Prop() data: number[] = [20, 45, 30, 60, 35, 70, 40];

  /**
   * Whether to animate the chart
   */
  @Prop() animated: boolean = true;

  /**
   * Chart variant: line-only, fill-only, or line-fill
   */
  @Prop() variant: TrendVariant = 'line-only';

  /**
   * Width of the chart in pixels
   */
  @Prop() width: number = 112;

  /**
   * Height of the chart in pixels
   */
  @Prop() height: number = 56;

  private normalizeData(): Array<{ x: number; y: number }> {
    const maxValue = Math.max(...this.data);
    const minValue = Math.min(...this.data);
    const range = maxValue - minValue || 1;

    return this.data.map((value, index) => ({
      x: (index / (this.data.length - 1)) * this.width,
      y: this.height - ((value - minValue) / range) * (this.height - 20) - 10,
    }));
  }

  private generatePath(points: Array<{ x: number; y: number }>): string {
    if (points.length < 2) return '';

    let path = `M ${points[0].x} ${points[0].y}`;

    for (let i = 1; i < points.length; i++) {
      const prev = points[i - 1];
      const curr = points[i];
      const tension = 0.5;

      const cp1x = prev.x + (curr.x - prev.x) * tension;
      const cp1y = prev.y;
      const cp2x = curr.x - (curr.x - prev.x) * tension;
      const cp2y = curr.y;

      path += ` C ${cp1x} ${cp1y}, ${cp2x} ${cp2y}, ${curr.x} ${curr.y}`;
    }

    return path;
  }

  private generateBackgroundPath(points: Array<{ x: number; y: number }>): string {
    if (points.length < 2) return '';

    let path = `M ${points[0].x} ${this.height}`;
    path += ` L ${points[0].x} ${points[0].y}`;

    for (let i = 1; i < points.length; i++) {
      const prev = points[i - 1];
      const curr = points[i];
      const tension = 0.5;

      const cp1x = prev.x + (curr.x - prev.x) * tension;
      const cp1y = prev.y;
      const cp2x = curr.x - (curr.x - prev.x) * tension;
      const cp2y = curr.y;
      path += ` C ${cp1x} ${cp1y}, ${cp2x} ${cp2y}, ${curr.x} ${curr.y}`;
    }

    path += ` L ${points[points.length - 1].x} ${this.height}`;
    path += ' Z';

    return path;
  }

  private getStrokeColor(): string {
    switch (this.trend) {
      case 'positive':
        return 'var(--color-success-500)';
      case 'negative':
        return 'var(--color-error-500)';
      case 'neutral':
        return 'var(--color-neutral-500)';
      default:
        return 'var(--color-neutral-500)';
    }
  }

  private getFillColor(): string {
    switch (this.trend) {
      case 'positive':
        return 'var(--color-success-600)';
      case 'negative':
        return 'var(--color-error-600)';
      case 'neutral':
        return 'var(--color-neutral-600)';
      default:
        return 'var(--color-neutral-600)';
    }
  }

  render() {
    const points = this.normalizeData();
    const pathData = this.generatePath(points);
    const backgroundPath = this.variant !== 'line-only' ? this.generateBackgroundPath(points) : '';
    const strokeColor = this.getStrokeColor();
    const fillColor = this.getFillColor();
    const gradientId = `gradient-${this.trend}`;

    return (
      <Host>
        <div
          class={{
            'trend-chart': true,
            [`trend-chart-${this.trend}`]: true,
            [`trend-chart-${this.variant}`]: true,
            'trend-chart-animated': this.animated,
          }}
        >
          <svg
            width={this.width}
            height={this.height}
            viewBox={`0 0 ${this.width} ${this.height}`}
            class="chart-svg"
          >
            <defs>
              <linearGradient id={gradientId} x1="0%" y1="0%" x2="0%" y2="100%">
                <stop
                  offset="0%"
                  stop-color={fillColor}
                  stop-opacity={this.variant === 'fill-only' ? 0.4 : 0.2}
                />
                <stop
                  offset="100%"
                  stop-color={fillColor}
                  stop-opacity={this.variant === 'fill-only' ? 0.1 : 0.05}
                />
              </linearGradient>
            </defs>

            {/* Background fill (for fill-only and line-fill variants) */}
            {backgroundPath && this.variant !== 'line-only' && (
              <path
                class="chart-background"
                d={backgroundPath}
                fill={`url(#${gradientId})`}
              />
            )}

            {/* Stroke line (for line-only and line-fill variants) */}
            {pathData && this.variant !== 'fill-only' && (
              <path
                class="chart-line"
                d={pathData}
                stroke={strokeColor}
                stroke-width="2"
                fill="none"
              />
            )}
          </svg>
        </div>
      </Host>
    );
  }
}
